import React, {useState} from 'react'
import s from './ProfileInfo.module.css'
import userPhoto from '../../../assets/images/user.png'
import ProfileStatusWithHooks from './ProfileStatusWithHooks'
import ProfileDataForm from './ProfileDataForm.jsx'
import vk from '../../../assets/images/vk.svg'
import instagram from '../../../assets/images/instagram.svg'
import skype from '../../../assets/images/skype.svg'
import twitter from '../../../assets/images/twitter.svg'
import youtube from '../../../assets/images/youtube.svg'
import facebook from '../../../assets/images/facebook.svg'

const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto, saveProfile}) => {
    let [editMode, setEditMode] = useState(false);

    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            savePhoto(e.target.files[0]);
        }
    }
//{isOwner && <input id="upl" type={"file"} onChange={onMainPhotoSelected}/>}

    const onSubmit = (formData) => {
        saveProfile(formData).then(
            () => setEditMode(false)
        )
    }

    const goToEditMode = () => {
        setEditMode(true)
    }

    return (
        <div className={s.infoBlock}>
            <span className={s.mainInfo}>
                <img src={profile.photos.large || userPhoto} className={s.mainPhoto} alt={""}/>
                <div className={s.name}>
                    {profile.fullName}
                </div>
                <div className={s.status}>
                    <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
                </div>
                {isOwner && <button className={s.edit} onClick={goToEditMode}>Edit my profile</button>}
            </span>
            {editMode
                ? <ProfileDataForm initialValues={profile} profile={profile} onSubmit={onSubmit}
                                   status={status} updateStatus={updateStatus}/>
                : <ProfileData profile={profile}/>}
        </div>
    )
}

const ProfileData = ({profile}) => {
    return <div className={s.description}>
        <div>
            <p>About me</p>
            <span className={s.tab}>{profile.aboutMe}</span>
        </div>

        {profile.lookingForAJob &&
        <div>
            <p>Skills</p>
            <span className={s.tab}>
                {profile.lookingForAJobDescription}
            </span>
        </div>}

        <div className={s.contacts}>
            <img src={vk} alt=""/>
            <img src={instagram} alt=""/>
            <img src={skype} alt=""/>
            <img src={twitter} alt=""/>
            <img src={youtube} alt=""/>
            <img src={facebook} alt=""/>


        </div>
    </div>
}

/*const Contact = ({contactTitle, contactValue}) => {
    if (contactValue)
    {Object.keys(profile.contacts).map(key => {
        return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
    })}
    {
        return <div><img src={contactTitle} alt=""/><a className={s.contactsLink} href={contactValue}>{contactValue}</a></div>
    } else {
        return null
    }
}*/

export default ProfileInfo;



